<template><section><h2>RecycleList 滚动列表</h2>
<p>可回收的滚动列表，始终将 DOM 的数量控制在一个很低的范围。</p>
<h3>注意事项</h3>
<ul>
<li>
<p>首先要实现无限滚动，必须保证要有一个固定高度的父容器元素包裹组件。</p>
</li>
<li>
<p>需要将未加载的图片用样式固定住避免高度计算错误。</p>
</li>
<li>
<p>内部的实现依赖 Promise，所以你需要引入 Promise polyfill 来做兼容。</p>
</li>
</ul>
<h3>基本用法</h3>
<div class="demo-block mt_15" style="height: 400px">
<tj-recycle-list class="list" :size="size" :on-fetch="onFetch" :offset="offset">
  <template slot="item" slot-scope="{ data }">
    <tj-row flex class="mt_15" :gutter="15" @click="handleClick(data)">
      <tj-col width="48px">
        <img width="100%" :src="data.avatar">
      </tj-col>
      <tj-col class="bubble">
        <p>{{ data.msg }}</p>
        <div class="meta">
          <time class="posted-date">{{ data.time }}</time>
        </div>
      </tj-col>
    </tj-row>
  </template>
</tj-recycle-list>

</div>
<demo-block>
                    <div slot="desc"><tj-recycle-list class="list" :size="size" :on-fetch="onFetch" :offset="offset">
  <template slot="item" slot-scope="{ data }">
    <tj-row flex class="mt_15" :gutter="15" @click="handleClick(data)">
      <tj-col width="48px">
        <img width="100%" :src="data.avatar">
      </tj-col>
      <tj-col class="bubble">
        <p>{{ data.msg }}</p>
        <div class="meta">
          <time class="posted-date">{{ data.time }}</time>
        </div>
      </tj-col>
    </tj-row>
  </template>
</tj-recycle-list>


</div>
                  <div slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">tj-recycle-list</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"list"</span> <span class="hljs-attr">:size</span>=<span class="hljs-string">"size"</span> <span class="hljs-attr">:on-fetch</span>=<span class="hljs-string">"onFetch"</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"offset"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"item"</span> <span class="hljs-attr">slot-scope</span>=<span class="hljs-string">"{ data }"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">tj-row</span> <span class="hljs-attr">flex</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"mt_15"</span> <span class="hljs-attr">:gutter</span>=<span class="hljs-string">"15"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleClick(data)"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">tj-col</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"48px"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"100%"</span> <span class="hljs-attr">:src</span>=<span class="hljs-string">"data.avatar"</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">tj-col</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">tj-col</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"bubble"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{ data.msg }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"meta"</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">time</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"posted-date"</span>&gt;</span>{{ data.time }}<span class="hljs-tag">&lt;/<span class="hljs-name">time</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">tj-col</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">tj-row</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">tj-recycle-list</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">size</span>: <span class="hljs-number">50</span>,
      <span class="hljs-attr">offset</span>: <span class="hljs-number">100</span>
    }
  },
  <span class="hljs-attr">methods</span>: {
    onFetch() {
      <span class="hljs-keyword">let</span> items = []
      <span class="hljs-keyword">return</span> <span class="hljs-keyword">new</span> <span class="hljs-built_in">Promise</span>(<span class="hljs-function">(<span class="hljs-params">resolve</span>) =&gt;</span> {
        <span class="hljs-comment">// 模拟请求 50 条数据，因为 size 设置为 50</span>
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; <span class="hljs-number">50</span>; i++) {
            items.push({
              <span class="hljs-attr">id</span>: i,
              <span class="hljs-attr">avatar</span>: <span class="hljs-string">'https://s3.amazonaws.com/uifaces/faces/twitter/danpliego/128.jpg'</span>,
              <span class="hljs-attr">msg</span>: <span class="hljs-string">'name'</span> + i,
              <span class="hljs-attr">time</span>: <span class="hljs-string">'Thu Oct 25 2018 15:02:12 GMT+0800 (中国标准时间)'</span>
            })
          }
          resolve(items)
        }, <span class="hljs-number">1000</span>)
      })
    },
    handleClick(data) {
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'Item:'</span> + data)
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3>Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>infinite</td>
<td>是否无限滚动模式</td>
<td>boolean</td>
<td></td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>一次加载的数据量</td>
<td>number</td>
<td></td>
<td>20</td>
</tr>
<tr>
<td>offset</td>
<td>底部拉取更多数据的距离</td>
<td>number</td>
<td></td>
<td>200</td>
</tr>
<tr>
<td>onFetch</td>
<td>获取更多数据</td>
<td>function</td>
<td>必传</td>
<td></td>
</tr>
</tbody>
</table>
<h3>Slots</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>item</td>
<td>列表某项的作用域插槽，通过此插槽实现想要的渲染视图</td>
</tr>
<tr>
<td>noMore</td>
<td>无更多数据的提示文案的具名插槽</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
    return {
      size: 50,
      offset: 100
    }
  },
  methods: {
    onFetch() {
      let items = []
      return new Promise((resolve) => {
        // 模拟请求 50 条数据，因为 size 设置为 50
        setTimeout(() => {
          for (let i = 0; i < 50; i++) {
            items.push({
              id: i,
              avatar: 'https://s3.amazonaws.com/uifaces/faces/twitter/danpliego/128.jpg',
              msg: 'name' + i,
              time: 'Thu Oct 25 2018 15:02:12 GMT+0800 (中国标准时间)'
            })
          }
          resolve(items)
        }, 1000)
      })
    },
    handleClick(data) {
      console.log('Item:' + data)
    }
  }
}
</script>


